"use client";

import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Switch } from "@/components/ui/switch";
import { Separator } from "@/components/ui/separator";

export default function SettingsPage() {
    return (
        <div className="space-y-6">
            <div>
                <h2 className="text-2xl font-bold">Account Settings</h2>
                <p className="text-muted-foreground">Manage your notification preferences and account security.</p>
            </div>
            <Separator />

            <div className="space-y-6">
                <div className="space-y-4">
                    <h3 className="text-lg font-semibold">Notifications</h3>
                    <div className="flex items-center justify-between">
                        <div className="space-y-0.5">
                            <Label className="text-base">Order Updates</Label>
                            <p className="text-sm text-muted-foreground">Receive emails about your order status and shipping.</p>
                        </div>
                        <Switch defaultChecked />
                    </div>
                    <Separator />
                    <div className="flex items-center justify-between">
                        <div className="space-y-0.5">
                            <Label className="text-base">Marketing Emails</Label>
                            <p className="text-sm text-muted-foreground">Get updates on new cuddles and special offers.</p>
                        </div>
                        <Switch defaultChecked />
                    </div>
                </div>

                <div className="space-y-4 pt-4">
                    <h3 className="text-lg font-semibold text-destructive">Danger Zone</h3>
                    <div className="p-4 border border-destructive/20 rounded-lg bg-destructive/5 flex items-center justify-between">
                        <div className="space-y-0.5">
                            <Label className="text-base text-destructive font-bold">Delete Account</Label>
                            <p className="text-sm text-muted-foreground">Permanently delete your account and all data.</p>
                        </div>
                        <Button variant="destructive">Delete Account</Button>
                    </div>
                </div>
            </div>
        </div>
    );
}
